
'use client';

import { useState, useEffect } from 'react';
import { useUser, useDoc, useMemoFirebase, errorEmitter, FirestorePermissionError } from '@/firebase';
import { collection, query, where, getDocs, doc } from 'firebase/firestore';
import { firestore } from '@/lib/firebase';
import Header from '@/components/layout/header';
import Footer from '@/components/layout/footer';
import { skillMatching, SkillMatchingOutput } from '@/ai/flows/skill-matching';
import { Avatar, AvatarFallback, AvatarImage } from '@/components/ui/avatar';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Badge } from '@/components/ui/badge';
import { Skeleton } from '@/components/ui/skeleton';
import { Repeat, ArrowRightLeft } from 'lucide-react';
import { Button } from '@/components/ui/button';

interface UserProfile {
  id: string;
  name: string;
  photoURL?: string;
  skillsToTeach: { name: string, verified: boolean }[];
  skillsToLearn: { name: string }[];
}

type MatchWithUser = SkillMatchingOutput[number] & {
  user?: UserProfile;
};

export default function SwapPage() {
  const { user, isUserLoading } = useUser();
  const [matches, setMatches] = useState<MatchWithUser[]>([]);
  const [isLoadingMatches, setIsLoadingMatches] = useState(true);
  const [error, setError] = useState<Error | null>(null);

  const currentUserDocRef = useMemoFirebase(() => (user ? doc(firestore, 'users', user.uid) : null), [user]);
  const { data: currentUserProfile, isLoading: isProfileLoading } = useDoc<UserProfile>(currentUserDocRef);

  useEffect(() => {
    const findMatches = async () => {
      if (!currentUserProfile) return;
      
      setIsLoadingMatches(true);
      setError(null);

      try {
        // 1. Fetch all users once
        const usersRef = collection(firestore, 'users');
        const querySnapshot = await getDocs(usersRef);

        const allUsersData: { [id: string]: UserProfile } = {};
        querySnapshot.forEach(docSnap => {
          const data = docSnap.data();
          allUsersData[docSnap.id] = {
            id: docSnap.id,
            name: data.name,
            photoURL: data.photoURL,
            skillsToTeach: data.skillsToTeach || [],
            skillsToLearn: data.skillsToLearn || [],
          };
        });
        
        // 2. Filter out the current user to find "other" users
        const otherUserProfilesForAI = Object.values(allUsersData)
            .filter(profile => profile.id !== currentUserProfile.id)
            .map(profile => ({
                userId: profile.id,
                skillsToTeach: profile.skillsToTeach.map(s => s.name),
                skillsToLearn: profile.skillsToLearn.map(s => s.name),
            }));

        // 3. Call the AI for matches
        const matchingInput = {
            userSkillsToLearn: currentUserProfile.skillsToLearn.map(s => s.name),
            userSkillsToTeach: currentUserProfile.skillsToTeach.map(s => s.name),
            allUserProfiles: otherUserProfilesForAI,
        };

        const aiMatches = await skillMatching(matchingInput);
        
        // 4. Enrich AI matches with user data from the initial fetch
        const detailedMatches: MatchWithUser[] = aiMatches.map(match => ({
          ...match,
          user: allUsersData[match.userId],
        }));

        setMatches(detailedMatches);
      } catch (err: any) {
        const permissionError = new FirestorePermissionError({
            path: 'users',
            operation: 'list',
        });
        setError(permissionError);
        errorEmitter.emit('permission-error', permissionError);
      } finally {
        setIsLoadingMatches(false);
      }
    };

    if (currentUserProfile) {
      findMatches();
    }
  }, [currentUserProfile]);

  const getInitials = (name: string | null | undefined) => {
    if (!name) return '';
    return name.split(' ').map((n) => n[0]).join('');
  };

  const isLoading = isUserLoading || isProfileLoading;

  return (
    <div className="flex flex-col min-h-screen">
      <Header />
      <main className="flex-1 py-20 md:py-28">
        <div className="container mx-auto px-4 sm:px-6 lg:px-8">
          <div className="text-center max-w-3xl mx-auto">
            <h1 className="text-4xl font-bold tracking-tight text-foreground sm:text-5xl font-headline">
              Find Your Perfect Swap
            </h1>
            <p className="mt-4 text-xl leading-8 text-muted-foreground">
              Our AI has analyzed the community to find the best skill-swap partners for you.
            </p>
          </div>

          <div className="mt-16 max-w-4xl mx-auto">
            {isLoadingMatches || isLoading ? (
                <div className="space-y-6">
                    {Array.from({ length: 3 }).map((_, i) => (
                        <Card key={i} className="bg-card/70 border-border/50">
                            <CardHeader className="flex flex-row items-center gap-4">
                                <Skeleton className="h-16 w-16 rounded-full" />
                                <div className="space-y-2">
                                    <Skeleton className="h-6 w-40" />
                                    <Skeleton className="h-4 w-12" />
                                </div>
                            </CardHeader>
                            <CardContent className="grid grid-cols-1 md:grid-cols-2 gap-6">
                                <div className="space-y-2">
                                    <Skeleton className="h-5 w-24" />
                                    <Skeleton className="h-8 w-full" />
                                </div>
                                <div className="space-y-2">
                                    <Skeleton className="h-5 w-24" />
                                    <Skeleton className="h-8 w-full" />
                                </div>
                            </CardContent>
                        </Card>
                    ))}
                </div>
            ) : error ? (
                 <div className="text-center py-16 text-red-400">
                    <Repeat className="mx-auto h-12 w-12" />
                    <h3 className="mt-4 text-xl font-semibold">Error Finding Matches</h3>
                    <p className="mt-2 text-red-400/80">There was a problem fetching potential matches. This is likely a permissions issue.</p>
                    <Button onClick={() => window.location.reload()} className="mt-6">Try Again</Button>
                </div>
            ) : matches.length > 0 ? (
              <div className="space-y-6">
                {matches.map((match) => (
                  <Card key={match.userId} className="bg-card/70 border-border/50 hover:border-primary/50 transition-all duration-300 hover:shadow-xl hover:shadow-primary/10">
                    <CardHeader className="flex flex-row items-center justify-between">
                        <div className="flex items-center gap-4">
                            <Avatar className="w-16 h-16 text-2xl ring-2 ring-primary/20">
                                <AvatarImage src={match.user?.photoURL || ''} alt={match.user?.name || ''} />
                                <AvatarFallback>{getInitials(match.user?.name)}</AvatarFallback>
                            </Avatar>
                            <div>
                                <h3 className="text-2xl font-bold font-headline">{match.user?.name}</h3>
                                <Badge variant="outline" className="mt-1">Relevance: {Math.round(match.relevanceScore * 100)}%</Badge>
                            </div>
                        </div>
                         <Button variant="outline" className="hidden sm:inline-flex">
                            <ArrowRightLeft className="mr-2 h-4 w-4" />
                            Propose Swap
                        </Button>
                    </CardHeader>
                    <CardContent className="grid grid-cols-1 md:grid-cols-2 gap-x-6 gap-y-4">
                      <div>
                        <h4 className="font-semibold text-muted-foreground mb-2">You Can Learn...</h4>
                        <div className="flex flex-wrap gap-2">
                          {match.matchedSkillsToLearn.map(skill => (
                            <Badge key={skill} variant="secondary" className="text-base">{skill}</Badge>
                          ))}
                        </div>
                      </div>
                      <div>
                        <h4 className="font-semibold text-muted-foreground mb-2">You Can Teach...</h4>
                        <div className="flex flex-wrap gap-2">
                          {match.matchedSkillsToTeach.map(skill => (
                            <Badge key={skill} variant="default" className="text-base bg-accent hover:bg-accent/80">{skill}</Badge>
                          ))}
                        </div>
                      </div>
                    </CardContent>
                  </Card>
                ))}
              </div>
            ) : (
              <div className="text-center py-16">
                <Repeat className="mx-auto h-12 w-12 text-muted-foreground" />
                <h3 className="mt-4 text-xl font-semibold text-foreground">No Matches Found Yet</h3>
                <p className="mt-2 text-muted-foreground">Try adding more skills to your profile to improve your chances of a match!</p>
                <Button onClick={() => window.location.reload()} className="mt-6">Refresh Matches</Button>
              </div>
            )}
          </div>
        </div>
      </main>
      <Footer />
    </div>
  );
}
